@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500;700&display=swap);
html {
  box-sizing: border-box;
  width: 100vw;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  background-image: url(/goit-react-hw-04-hooks-images/static/media/photos.52bd0a76.jpg);
  font-family: 'Quicksand', sans-serif;
  color: #212121;
  background-color: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
p {
  margin: 0;
}

h1,
h2,
h3,
button {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

li {
  list-style: none;
}

